    font-size: 16px;
    font-weight: 400;
    text-align: justify;
    margin-top: 0;
}

.wsmenu > .wsmenu-list > li > .wsmegamenu .link-list li {
    display: block;
    text-align: center;
    text-align: left;
    border-bottom: 1px dashed #c0c0c0;
