In mobile it doesn not allow scrolling probably due to mobile menu differences. but this causes the canvas not to be able to scroll 

  html {
    overflow: hidden;
    height: 100%;
    -webkit-overflow-scrolling: touch;
  }

  body {
    height: 100%;
    overflow-y: auto;
    overflow-x: hidden;
  }

  body.wsactive {
    overflow: hidden;
  }

  */
    html {
        overflow: auto;
        height: 100%;
        -webkit-overflow-scrolling: touch;
    }

    /*
  body {
    height: 100%;
    overflow-y: auto;
    overflow-x: auto;
  }

  body.wsactive {
    overflow: auto;
  }

  */

    /* ================== Mobile Main Menu CSS ================== */
    .desktoplogo {
        display: none;
    }

    .wsmainfull {
        height: 0;
    }

    .wsmenucontainer {
        background-attachment: local;
        background-position: 33% 0%;
    }

    .wsmenu {
        width: 100%;
        background: rgba(0, 0, 0, 0) none repeat scroll 0 0;
        left: 0;
        overflow-y: hidden;
        padding: 0;
        top: 0;
        visibility: hidden;
        position: fixed;
        margin: 0px;
    }

    .wsmenu > .wsmenu-list {
        height: auto;
        min-height: 100%;
        width: 240px;
        width: 300px;
        background: #fff;
        padding-bottom: 0;
        margin-left: -240px;
        margin-left: -300px;
        display: block;
        text-align: center;
        -webkit-transition: all 0.25s ease-in-out;
